'use client';
import { usePathname } from 'next/navigation';
import Link from 'next/link';

export default function Sidebar({ items = [], isOpen, onToggle }) {
  const pathname = usePathname();

  if (!Array.isArray(items)) {
    console.error('Sidebar: items prop must be an array');
    return null;
  }

  return (
    <div className={`bg-gray-800 text-white ${isOpen ? 'w-64' : 'w-20'} min-h-screen transition-all duration-300 ease-in-out`}>
      <div className="p-4 flex justify-between items-center border-b border-gray-700">
        {isOpen && <h2 className="text-xl font-bold">TMS</h2>}
        <button 
          onClick={onToggle}
          className="p-2 rounded-lg hover:bg-gray-700"
          aria-label={isOpen ? 'Collapse sidebar' : 'Expand sidebar'}
        >
          {isOpen ? '«' : '»'}
        </button>
      </div>
      <nav className="mt-6">
        <ul>
          {items.map((item, index) => (
            <li key={index} className="mb-2">
              <Link 
                href={item.link} 
                className={`flex items-center p-3 ${pathname === item.link ? 'bg-blue-700' : 'hover:bg-gray-700'} rounded-lg`}
              >
                <span className="mr-3">{item.icon}</span>
                {isOpen && <span>{item.title}</span>}
              </Link>
            </li>
          ))}
        </ul>
      </nav>
    </div>
  );
}